import { useState, useEffect } from "react"

interface ScanStatus {
  isScanning: boolean
  progress: number
  currentFile: string
  threatsFound: number
}

interface Threat {
  id: string
  type: string
  path: string
  severity: string
  timestamp: string
}

export function useScan() {
  const [status, setStatus] = useState<ScanStatus>({
    isScanning: false,
    progress: 0,
    currentFile: "",
    threatsFound: 0,
  })
  const [threats, setThreats] = useState<Threat[]>([])

  const startScan = async (type: "quick" | "full" | "custom") => {
    try {
      const response = await fetch("/api/scan", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ scanType: type }),
      })

      if (!response.ok) throw new Error("Failed to start scan")

      setStatus(prev => ({
        ...prev,
        isScanning: true,
        progress: 0,
        threatsFound: 0,
      }))

      // Simulate scan progress
      const interval = setInterval(() => {
        setStatus(prev => {
          if (prev.progress >= 100) {
            clearInterval(interval)
            return {
              ...prev,
              isScanning: false,
              currentFile: "",
              progress: 100,
            }
          }

          const increment = Math.random() * 5
          const newProgress = Math.min(prev.progress + increment, 100)

          return {
            ...prev,
            progress: newProgress,
            currentFile: `Scanning system files... (${Math.round(newProgress)}%)`,
          }
        })
      }, 500)

      // Cleanup interval on component unmount
      return () => clearInterval(interval)
    } catch (error) {
      console.error("Scan failed:", error)
      setStatus(prev => ({
        ...prev,
        isScanning: false,
        currentFile: "",
      }))
    }
  }

  const stopScan = () => {
    setStatus(prev => ({
      ...prev,
      isScanning: false,
      currentFile: "",
    }))
  }

  const fetchThreats = async () => {
    try {
      const response = await fetch("/api/scan")
      if (!response.ok) throw new Error("Failed to fetch threats")
      
      const data = await response.json()
      if (data.success && data.data.threats) {
        setThreats(data.data.threats)
      }
    } catch (error) {
      console.error("Failed to fetch threats:", error)
    }
  }

  useEffect(() => {
    fetchThreats()
  }, [])

  return {
    status,
    threats,
    startScan,
    stopScan,
    fetchThreats,
  }
}
