"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Shield, ShieldCheck, ShieldAlert, Loader2 } from "lucide-react"
import { useScan } from "@/hooks/use-scan"
import { useState } from "react"
import "@/styles/antivirus.css"

export default function Home() {
  const { status, threats, startScan, stopScan } = useScan()
  const [scanType, setScanType] = useState<"quick" | "full" | "custom">("quick")
  return (
    <main className="min-h-screen p-8 bg-gradient-to-b from-gray-50 to-gray-100">
      {/* Header Section */}
      <div className="max-w-5xl mx-auto space-y-8">
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold tracking-tight">Zuu Caster Next Generation</h1>
          <p className="text-gray-500">Advanced protection for your digital world</p>
        </div>

        {/* Status Card */}
        <Card className="p-6 protection-status">
          <div className="flex items-center justify-between mb-6 relative">
            <div className="flex items-center space-x-4">
              <ShieldCheck className="w-8 h-8 text-green-500" />
              <div>
                <h2 className="text-xl font-semibold">System Protected</h2>
                <p className="text-sm text-gray-500">Last scan: 2 hours ago</p>
              </div>
            </div>
            <Button 
              size="lg" 
              className="bg-blue-600 hover:bg-blue-700"
              onClick={() => startScan(scanType)}
              disabled={status.isScanning}
            >
              {status.isScanning ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Scanning...
                </>
              ) : (
                "Start Scan"
              )}
            </Button>
          </div>

          {/* Protection Status */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <Card className="p-4 border-green-100 card-hover">
              <div className="flex items-center space-x-3">
                <Shield className="w-5 h-5 text-green-500" />
                <div>
                  <p className="font-medium">Real-time Protection</p>
                  <p className="text-sm text-green-600">Active</p>
                </div>
              </div>
            </Card>
            <Card className="p-4 border-green-100 card-hover">
              <div className="flex items-center space-x-3">
                <Shield className="w-5 h-5 text-green-500" />
                <div>
                  <p className="font-medium">Firewall</p>
                  <p className="text-sm text-green-600">Enabled</p>
                </div>
              </div>
            </Card>
            <Card className="p-4 border-green-100 card-hover">
              <div className="flex items-center space-x-3">
                <Shield className="w-5 h-5 text-green-500" />
                <div>
                  <p className="font-medium">Web Protection</p>
                  <p className="text-sm text-green-600">Active</p>
                </div>
              </div>
            </Card>
          </div>

          {/* Scan Progress */}
          {status.isScanning && (
            <div className="space-y-2">
              <div className="flex justify-between text-sm">
                <span>{status.currentFile}</span>
                <span>{Math.round(status.progress)}%</span>
              </div>
              <Progress value={status.progress} className="h-2" />
            </div>
          )}
        </Card>

        {/* Threat Detection */}
        <Card className="p-6 protection-status">
          <div className="flex items-center space-x-4 mb-6">
            <div className="p-2 bg-yellow-50 rounded-full">
              <ShieldAlert className="w-6 h-6 text-yellow-500" />
            </div>
            <div>
              <h2 className="text-xl font-semibold">Recent Threats</h2>
              <p className="text-sm text-gray-500">Monitoring system activity</p>
            </div>
          </div>
          
          <div className="space-y-4">
            {threats.map((threat) => (
              <Alert 
                key={threat.id} 
                variant={threat.severity === "high" ? "destructive" : "default"}
                className="border-l-4 animate-fade-in"
              >
                <AlertTitle className="flex items-center space-x-2">
                  <span>{threat.type === "malware" ? "Malware Detected" : "Suspicious Activity"}</span>
                  {threat.severity === "high" && (
                    <span className="px-2 py-1 text-xs bg-red-100 text-red-800 rounded-full">High Risk</span>
                  )}
                </AlertTitle>
                <AlertDescription>
                  <p className="font-mono text-sm">{threat.path}</p>
                  <p className="text-sm text-gray-500 mt-2 flex items-center space-x-4">
                    <span>Severity: {threat.severity}</span>
                    <span>•</span>
                    <span>Detected: {new Date(threat.timestamp).toLocaleString()}</span>
                  </p>
                </AlertDescription>
              </Alert>
            ))}
            {threats.length === 0 && (
              <Alert className="bg-green-50 border-green-200">
                <AlertTitle className="text-green-800">No Threats Found</AlertTitle>
                <AlertDescription className="text-green-700">
                  Your system is currently clean and secure
                </AlertDescription>
              </Alert>
            )}
          </div>
        </Card>

        {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <Card 
            className={`scan-type-card p-6 cursor-pointer ${
              scanType === "quick" ? "selected" : ""
            }`}
            onClick={() => setScanType("quick")}
          >
            <h3 className="font-semibold mb-2">Quick Scan</h3>
            <p className="text-sm text-gray-500">Scan essential system files</p>
          </Card>
          <Card 
            className={`scan-type-card p-6 cursor-pointer ${
              scanType === "full" ? "selected" : ""
            }`}
            onClick={() => setScanType("full")}
          >
            <h3 className="font-semibold mb-2">Full System Scan</h3>
            <p className="text-sm text-gray-500">Complete system analysis</p>
          </Card>
          <Card 
            className={`scan-type-card p-6 cursor-pointer ${
              scanType === "custom" ? "selected" : ""
            }`}
            onClick={() => setScanType("custom")}
          >
            <h3 className="font-semibold mb-2">Custom Scan</h3>
            <p className="text-sm text-gray-500">Select areas to scan</p>
          </Card>
        </div>
      </div>
    </main>
  )
}
